@import '../../node_modules/@splidejs/splide/dist/css/splide.min.css';


@import '../../partials/slider/slider';
@import '../../partials/slider/listing/listing';

.layout-section {
    .left-section {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        .layout-name {
            display: flex;
            align-items: center;
            .logo-star {
                margin-right: 15px;
            }
            h1 {
                font-size: 18px;
            }
        }
        .back {
            font-size: 16px;
            font-weight: 700;
            text-transform: lowercase;
        }
    }
    .layout-slider {
        margin: 10px 30px 0 0;
        position: relative;
        padding-bottom: 15%;
    }
    .pagination-slider {
        position: absolute;
        right: 0;
        bottom: 0;
        ul {
            display: flex;
            button {
                color: #fff;
            }
            .pagination-slide-item {
                button {
                    position: relative;
                    transition: all .2s ease-in-out;
                    opacity: .7;
                    margin: 0 7px;
                    line-height: 1;
                    &::before {
                        content: '';
                        position: absolute;
                        bottom: calc(100% + 5px);
                        left: calc(50% - 1px);
                        width: 1px;
                        height: 9px;
                        background-color: #fff;
                        transition: all 500ms ease-in-out;
                        opacity: 0;
                    }
                    &:hover {
                        &::before {
                            opacity: 1;
                            // transform: scale(1.5);
                        }
                    }
                }
                
            }
            .pagination-slide-item.active {
                button {
                    opacity: 1;
                    transform: scale(1.5);
                    &::before {
                        transform: scaleX(1.33);
                        opacity: 1;
                    }
                }
            }
        }
    }
    .splide__arrow--next {
        right: -80px;
    }
    .splide__arrow--prev {
        left: -80px;
    }
    .splide__arrow {
        display: none;
        opacity: 0.2;
        background-color: transparent;
        svg {
            width: 26px;
            height: 26px;
            fill: #fff;
        }
    }
    .splide__track {
        overflow: visible;
    }
    .slide-wrap {
        flex: 1;
        display: flex;
        // margin: 8px 30px 8px 100px;
    }
    .layout-card-wrap {
        width: 33.3%;
        flex: 0 1 33.3%;
        position: relative;
        a {
            text-decoration: none;
        }
        .card-count {
            font-size: 18px;
            font-weight: 700;
            margin-bottom: 15px;
        }
        .card-image {
            // height: 133px;
            padding-bottom: 54%;
            background-size: cover;
            background-position: center top;
            margin-bottom: 15px;
        }
        .card-title {
            font-size: 18px;
            font-weight: 700;
            margin-bottom: 20px;
        }
        .card-description {
            line-height: 2.3;
            margin-bottom: 30px;
        }
        .more-details {
            font-size: 17px;
            font-weight: 700;
        }
    }
    .layout-card {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 0 20px;
        
    }
    .layout-card:hover .hidden-content {
        visibility: visible;
        opacity: 1;
    }
    .hidden-content {
        visibility: hidden;
        padding: 0 19px 55px;
        position: absolute;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        bottom: -60px;
        right: 0;
        left: 0;
        top: -260px;
        background-color: rgba($color: #fff, $alpha: .5);
        opacity: 0;
        transition: all .3s ease-in-out;
        background: linear-gradient(179.86deg, #000000 28.19%, rgba(0, 0, 0, 0) 91.01%);
        border-left-width: 1px;
        border-right-width: 1px;
        border-left-style: solid;
        border-right-style: solid;
        border-image: linear-gradient(to bottom, #fff, rgba(0, 0, 0, 0)) 1 100%;;
        .full-image {
            width: 100%;
            height: calc(100% - 299px);
            background-size: cover;
            background-position: center top;
        }
    }
}