@import '../../node_modules/@splidejs/splide/dist/css/splide.min.css';
.default-slider .slider-pagination, .listing-slider .slider-pagination {
  display: flex;
}
.default-slider .slider-pagination .pagination-item button, .listing-slider .slider-pagination .pagination-item button {
  position: relative;
  transition: all 0.2s ease-in-out;
  opacity: 0.7;
  margin: 0 7px;
  line-height: 1;
}
.default-slider .slider-pagination .pagination-item button::before, .listing-slider .slider-pagination .pagination-item button::before {
  content: "";
  position: absolute;
  bottom: calc(100% + 5px);
  left: calc(50% - 1px);
  width: 1px;
  height: 9px;
  background-color: #fff;
  transition: all 500ms ease-in-out;
  opacity: 0;
}
.default-slider .slider-pagination .pagination-item button:hover::before, .listing-slider .slider-pagination .pagination-item button:hover::before {
  opacity: 1;
}
.default-slider .slider-pagination .pagination-item.active button, .listing-slider .slider-pagination .pagination-item.active button {
  opacity: 1;
  transform: scale(1.5);
}
.default-slider .slider-pagination .pagination-item.active button::before, .listing-slider .slider-pagination .pagination-item.active button::before {
  transform: scaleX(1.33);
  opacity: 1;
}

.listing-slider {
  margin: 10px 30px 0 0;
  position: relative;
  padding-bottom: 120px;
}
.listing-slider .slider-pagination {
  position: absolute;
  right: 0;
  bottom: 0;
}
.listing-slider .splide__track {
  overflow: visible;
}
.listing-slider .slide-wrap {
  flex: 1;
  display: flex;
}
.listing-slider .splide__arrow {
  display: none;
}
.listing-slider .layout-card-wrap {
  width: 33.3%;
  flex: 0 1 33.3%;
  position: relative;
}
.listing-slider .layout-card-wrap a {
