}
.listing-slider .layout-card-wrap .layout-card {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0 20px;
}
.listing-slider .layout-card-wrap .layout-card:hover .hidden-content {
  visibility: visible;
  opacity: 1;
}
.listing-slider .layout-card-wrap .layout-card .card-count {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 15px;
}
.listing-slider .layout-card-wrap .layout-card .card-image {
