}
.listing-slider .layout-card-wrap .layout-card .more-details {
  font-size: 17px;
  font-weight: 700;
}
.listing-slider .layout-card-wrap .layout-card .hidden-content {
  visibility: hidden;
  padding: 0 19px 55px;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  bottom: -60px;
  right: 0;
  left: 0;
  top: -256px;
  background-color: rgba(255, 255, 255, 0.5);
  opacity: 0;
  transition: all 0.3s ease-in-out;
  background: linear-gradient(179.86deg, #000000 28.19%, rgba(0, 0, 0, 0) 91.01%);
  border-left-width: 1px;
  border-right-width: 1px;
  border-left-style: solid;
  border-right-style: solid;
  border-image: linear-gradient(to bottom, #fff, rgba(0, 0, 0, 0)) 1 100%;
}
.listing-slider .layout-card-wrap .layout-card .hidden-content .full-image {
  width: 100%;
