.listing-slider {
    margin: 10px 30px 0 0;
    position: relative;
    padding-bottom: 120px;
    .slider-pagination {
        position: absolute;
        right: 0;
        bottom: 0;
    }
    .splide__track {
        overflow: visible;
    }
    .slide-wrap {
        flex: 1;
        display: flex;
        // margin: 8px 30px 8px 100px;
    }
    .splide__arrow {
        display: none;
    }
    .layout-card-wrap {
        width: 33.3%;
        flex: 0 1 33.3%;
        position: relative;
        a {
            text-decoration: none;
        }
        .layout-card {
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding: 0 20px;
            &:hover {
                .hidden-content {
                    visibility: visible;
                    opacity: 1;
                }
            }
            .card-count {
                font-size: 18px;
                font-weight: 700;
                margin-bottom: 15px;
            }
            .card-image {
                // height: 133px;
                padding-bottom: 54%;
                background-size: cover;
                background-position: center top;
                margin-bottom: 15px;
            }
            .card-title {
                font-size: 18px;
                font-weight: 700;
                margin-bottom: 20px;
            }
            .card-description {
                line-height: 2.3;
                margin-bottom: 30px;
            }
            .more-details {
                font-size: 17px;
                font-weight: 700;
            }
            .hidden-content {
                visibility: hidden;
                padding: 0 19px 55px;
                position: absolute;
                display: flex;
                flex-direction: column;
                justify-content: flex-end;
                bottom: -60px;
                right: 0;
                left: 0;
                top: -256px;
                background-color: rgba($color: #fff, $alpha: .5);
                opacity: 0;
                transition: all .3s ease-in-out;
                background: linear-gradient(179.86deg, #000000 28.19%, rgba(0, 0, 0, 0) 91.01%);
                border-left-width: 1px;
                border-right-width: 1px;
                border-left-style: solid;
                border-right-style: solid;
                border-image: linear-gradient(to bottom, #fff, rgba(0, 0, 0, 0)) 1 100%;;
                .full-image {
                    width: 100%;
                    height: calc(100% - 299px);
                    background-size: cover;
                    background-position: center top;
                }
            }
        }
    }
}
.listing-slider.one-slide {
    .slider-pagination {
        display: none;
    }
}

