.default-slider, .listing-slider {
    .slider-pagination {
        display: flex;
        .pagination-item {
            button {
                position: relative;
                transition: all .2s ease-in-out;
                opacity: .7;
                margin: 0 7px;
                line-height: 1;
                &::before {
                    content: '';
                    position: absolute;
                    bottom: calc(100% + 5px);
                    left: calc(50% - 1px);
                    width: 1px;
                    height: 9px;
                    background-color: #fff;
                    transition: all 500ms ease-in-out;
                    opacity: 0;
                }
                &:hover {
                    &::before {
                        opacity: 1;
                        // transform: scale(1.5);
                    }
                }
            }
            
        }
        .pagination-item.active {
            button {
                opacity: 1;
                transform: scale(1.5);
                &::before {
                    transform: scaleX(1.33);
                    opacity: 1;
                }
            }
        }
    }
}